@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,500;1,300&display=swap');

body,
html {
  font-family: Raleway;
  font-weight: 400;
  margin: 0;
}

* {
  scroll-behavior: smooth;
  transition: 0.2s ease;
}

::selection {
  padding: 5px;
  border-radius: 5px;
  background-color: rgba(255, 0, 0, 0.3);
}

.w_shadow {
  -webkit-box-shadow: -2px 3px 37px 0px rgba(39, 2, 2, 0.2);
  -moz-box-shadow: -2px 3px 37px 0px rgba(39, 2, 2, 0.2);
  box-shadow: -2px 3px 37px 0px rgba(39, 2, 2, 0.2);
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.href {
  transition: 0.2s ease;
}

.href:hover {
  transform: translateY(-3px);
}

header {
  min-width: 500px;
  max-width:95%;
  padding: 10px;
  margin: 0 auto;
  display: flex;
  min-height:10vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

header div img {
  width: 100px;
}

header div ul {
  font-weight: 500;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
}

.active {
  color: red;
}

.atricles_all {
  display: flex;
  flex-wrap: wrap;
  width: 92%;
  margin: 0 auto;
  justify-content: space-around;
  align-items: center;
}

.atricle_box {
  overflow: hidden;
  border-radius: 5px;
  margin-bottom: 30px;
  padding: 15px;
  width: 300px;
  margin-left: 10px;
}

.img_box_art {
  display: flex;
  margin: 20px;
}

.img_box_art img {
  margin-left: -120px;
  margin-top: -140px;
}

button {
  background-color: red;
  transition: 0.2s ease;
  color: white;
  font-size: 14px;
  border-radius: 10px;
  padding: 7px 12px;
  text-align: center;
  border: 2px solid transparent;
  width: 80%;
  cursor: pointer;
  font-family: Raleway, sans-serif;
  margin-bottom: 10px;

}

button:hover {
  background-color: white;
  border: 2px solid red;
  color: red;
}

.item_prices {
  margin-left: -120px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.item_price {
  transform: translateX(50px);
  transition: 0.2s ease;
  width: 70px;
  padding: 7px 12px;
  text-align: left;
  font-weight: 500;
  font-size: 20px;
  cursor: pointer;
  background-color: black;
  color: white;
  margin-bottom: 7px;
  border-radius: 10px 0 0 10px;
}

.item_price:hover {
  transform: translateX(0);
}

.article_subbbox {
  margin-top: -220px;
}

.description_contacts {
  width: 410px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.hv_img:hover {
  transform: translateY(-3px);
}
.loading_line_top {
  width: 100%;
  height: 3px;
  position: fixed;
}
.loading_line_main {
  background: red;
  position: absolute;
  height: 100%;
  animation: loadingLine 1s ease;
}
input, textarea {
  width: 300px;
  padding: 7px 12px;
  border-radius: 10px;
  border: 2px solid black;
  resize: none;
  font-size: 14px;
  margin-bottom: 10px;
}
@keyframes loadingLine {
  0% {
    width: 10%;
    opacity: 1;
  }
  100% {
    width: 100%;
    opacity: 0;
  }
}
@media screen and(max-width: 800px) {
  .atricle_box {
    width: 250px !important;
  }
  
  .img_box_art {
    
  }
  
  .img_box_art img {

  }
}
